import React from "react";
import $ from "jquery";

let boxHeight = "";
let boxWidth = "";

const createDiv = (amountRow, amountColumn, marginBox, borderRadius) => {
  let index = 0;
  let boxArray = [];
  let amount = amountRow * amountColumn;
  let style = {
    margin: marginBox,
    width: boxWidth / amountRow - marginBox * 2,
    height: boxHeight / amountColumn - marginBox * 2,
    borderRadius: borderRadius,
  };
  while (index < amount) {
    index++;

    if (amount % 2 !== 0) {
      if (index === (amount + 1) / 2) {
        boxArray = [
          ...boxArray,
          <div key={index} style={style} className="specialBox"></div>,
        ];
      } else {
        boxArray = [
          ...boxArray,
          <div key={index} style={style} className="box"></div>,
        ];
      }
    } else if (amount % 2 === 0) {
      if (index === amount / 2) {
        boxArray = [
          ...boxArray,
          <div key={index} style={style} className="specialBox"></div>,
        ];
      } else {
        boxArray = [
          ...boxArray,
          <div key={index} style={style} className="box"></div>,
        ];
      }
    }
  }

  return boxArray;
};

export class DynimcTiles extends React.Component {
  componentDidMount() {
    this.props.setHeight($(window).height());
    this.props.setWidth($(window).width());
    window.addEventListener("resize", () => {
      this.props.setHeight($(window).height());
      this.props.setWidth($(window).width());
    });
  }

  componentWillUnmount() {
    window.removeEventListener("resize", () => {});
  }

  render() {
    boxHeight = this.props.height;
    boxWidth = this.props.width;
    return (
      <>
        <div id="box-container">{createDiv(3, 5, 5, 15).map((obj) => obj)}</div>
      </>
    );
  }
}
