import "@fontsource/roboto/300.css";
import "@fontsource/roboto/400.css";
import "@fontsource/roboto/500.css";
import "@fontsource/roboto/700.css";
import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import reportWebVitals from "./reportWebVitals";
import { DynimcTiles } from "./DynamicTiles";
import { configureStore, createAction, createReducer } from "@reduxjs/toolkit";
import { Provider, connect } from "react-redux";

const setHeight = createAction("SETHEIGHT");
const setWidth = createAction("SETWIDTH");

const initialState = { height: "", width: "" };

const documentStatesReducer = createReducer(initialState, (builder) => {
  builder
    .addCase(setHeight, (state, action) => {
      state.height = action.payload;
    })

    .addCase(setWidth, (state, action) => {
      state.width = action.payload;
    })
    .addDefaultCase((state, action) => state);
});

const store = configureStore({
  reducer: documentStatesReducer,
  middleware: (getDefaultMiddleware) => getDefaultMiddleware(),
});

const mapStateToProps = (state) => {
  return {
    width: state.width,
    height: state.height,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setHeight: (payload) => {
      dispatch(setHeight(payload));
    },
    setWidth: (payload) => {
      dispatch(setWidth(payload));
    },
  };
};
const DynamicTilesWithPropsAndStates = connect(
  mapStateToProps,
  mapDispatchToProps
)(DynimcTiles);
const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <Provider store={store}>
    <React.StrictMode>
      <DynamicTilesWithPropsAndStates />
    </React.StrictMode>
  </Provider>
);

reportWebVitals(console.log());
